import utils from '../utils/method.js'
export default{
	data(){
		return{
			amount:"",
			phoneNumber:"",
			zfbAccount:"",
			zfbName:"",
			info:null,
		}
	},
	methods:{
		async withDraw(){
			let _data = [
				{
					data:this.amount.trim(),
					info:'提现金额不能为空'
				},
				{
					data:isNaN(this.amount.trim()) ? "" : 1,
					info:'提现金额格式不正确'
				},
				{
					data:this.amount.trim() > this.info.EnableProfit ? "" : 1,
					info:'可提现金额不足'
				},
				{
					data:this.zfbAccount.trim(),
					info:'支付宝账号不能为空'
				},
				{
					data:this.zfbName.trim(),
					info:'支付宝姓名不能为空'
				},
				{
					data:this.phoneNumber.trim(),
					info:'手机号不能为空'
				},
				{
					data:/^[1][3,4,5,7,8][0-9]{9}$/.test(this.phoneNumber.trim()) ? "1" : "",
					info:'手机号格式不正确'
				},
			]
			let jres = await utils.judgeData(_data);
			if(jres){
				await this.$http({
					apiName:"withdraw",
					method:"POST",
					data:{
						amount:this.amount,
						phoneNumber:this.phoneNumber,
						zfbAccount:this.zfbAccount,
						zfbName:this.zfbName,
					}
				}).then(res => {
					uni.showModal({
					    title: '提示',
					    content: '提现申请提交成功，请注意短信通知',
						showCancel:false,
					    success: function (res) {
					        if (res.confirm) {
					            uni.navigateBack({
					            	delta:2,
					            })
					        }
					    }
					});
					
				}).catch(e=>{})
			}
			
		},
		async getPromoteInfo(){
			let res = await this.$http({
				apiName:"getPromoteInfo",
			})
			try{
				this.info = res;
			}catch(e){}
		},
	},
	async onLoad() {
		uni.showLoading({
			title:"数据加载中..."
		})
		await this.getPromoteInfo();
		uni.hideLoading()
	}
}
